import Login from '@/components/Login';//
import Register from '@/components/Register'//
import PersonalCenter from '@/components/PersonalCenter'//
import PersonalData from '@/components/PersonalData'//
import WalletDetils from '@/components/WalletDetils'//
import RedEnvelopes from '@/components/RedEnvelopes'//
import HandRedEnvelopes from '@/components/HandRedEnvelopes' //
import RedEnvelopesDetails from '@/components/RedEnvelopesDetails'//
import IntegralDetails from '@/components/IntegralDetails' //
import MyExtension from '@/components/MyExtension'//
import OrderDetails from '@/components/OrderDetails' //
import ExtensionDetails from '@/components/ExtensionDetails' // 
import UpdatePersonalData from '@/components/UpdatePersonalData' //
import MoneyDetails from '@/components/MoneyDetails'//

export default [
  {
    path:'/login',
    component: Login,
    name:'Login'
  },
  {
    path:'/',
    component: Register,
    name:'Register'
  },
  {
    path:'/personalData',
    component: PersonalData,
    name:'PersonalData'
  },
  {
    path:'/personalCenter',
    component: PersonalCenter,
    name:'PersonalCenter'
  },
  {
    path:'/walletDetils',
    component: WalletDetils,
    name:'WalletDetils'
  },
  {
    path:'/redEnvelopes',
    component: RedEnvelopes,
    name:'RedEnvelopes'
  },
  {
    path:'/handRedEnvelopes',
    component: HandRedEnvelopes,
    name:'HandRedEnvelopes'
  },
  {
    path:'/redEnvelopesDetails',
    component: RedEnvelopesDetails,
    name:'RedEnvelopesDetails'
  },
  {
    path:'/integralDetails',
    component: IntegralDetails,
    name:'IntegralDetails'
  },
  {
    path:'/myExtension',
    component: MyExtension,
    name:'MyExtension'
  },
  {
    path:'/orderDetails',
    component: OrderDetails,
    name:'OrderDetails'
  },
  {
    path:'/extensionDetails',
    component: ExtensionDetails,
    name:'ExtensionDetails'
  },
  {
    path:'/updatePersonalData',
    component: UpdatePersonalData,
    name:'UpdatePersonalData'
  },
  {
    path:'/moneyDetails',
    component: MoneyDetails,
    name:'MoneyDetails'
  },


]



